body {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    min-height: 100vh;
    position: relative;
}

.items {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
    justify-content: center;
    padding-bottom: 10%;
}

.item {
    width: 300px;
    height: 300px;
    background-color: orange;
    margin: 1%;
    border: 1px solid black;
    position: relative;
}

.item img {
    width: 80%;
    left: 50%;
    position: relative;
    transform: translate(-50%, 0%);
}

.blocked {
    width: 90%;
    height: 15%;
    position: absolute;
    left: 50%;
    bottom: 5%;
    transform: translate(-50%, 0%);
    border: 2px solid black;
    border-radius: 15px;
    font-size: x-large;
    color: white;
    background-color: rgb(26, 97, 26);
    cursor: not-allowed;
}

.not-blocked {
    width: 90%;
    height: 15%;
    position: absolute;
    left: 50%;
    bottom: 5%;
    transform: translate(-50%, 0%);
    border: 2px solid black;
    border-radius: 15px;
    cursor: pointer;
    font-size: x-large;
    color: white;
    background-color: rgb(55, 216, 55);
}

.fa-magnifying-glass {
    position: absolute;
    left: 5%;
    top: 5%;
    font-size: x-large;
    cursor: pointer;
}
@media screen and (max-width: 700px) {
    .items{
        padding-bottom: 150px;
    }
}